/* Form container with animated shadow effect */
.search-form {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 1200px;
  padding: 20px;
  padding-bottom: 40px;
  margin-top: 40px;
  padding-top: 40px;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  border: 1px solid #0066cc;
  margin-left: 30px;
  
  /* Initial box-shadow settings before animation */
  box-shadow: 0 0 20px rgba(0, 102, 204, 0.7);
  
  /* Animation for the form and shadow */
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 0.5s forwards, animateShadow 3s infinite alternate ease-in-out;
  animation-delay: 0.4s;
}

/* Keyframes for the form fade-in */
@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Keyframes for animated shadow using blue monochromatic colors */
@keyframes animateShadow {
  0% {
    box-shadow: 0 0 20px rgba(0, 102, 204, 0.7);
  }
  25% {
    box-shadow: 0 0 30px rgba(0, 77, 153, 0.7);
  }
  50% {
    box-shadow: 0 0 40px rgba(0, 51, 102, 0.7);
  }
  75% {
    box-shadow: 0 0 50px rgba(0, 25, 51, 0.7);
  }
  100% {
    box-shadow: 0 0 60px rgba(0, 102, 204, 0.7);
  }
}

/* For the heading animation and hover effect */
.titttle {
  font-weight: 800;
  text-align: center;
