body {
  font-family: 'Poppins', sans-serif;
  background-color: #f0f4f8;
  color: black;
  margin: 0;
  padding: 0;
}

.itinerary-form-container {
  max-width: 800px;
  margin: 2rem auto;
  margin-top: 60px;
  padding: 2rem;
  padding-top: 60px;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.547);
  color: black;


}

.itinerary-form-container h2 {
  color: #2c3e50;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  padding-top: 30px;
  font-weight: 700;
}

.itinerary-form {
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  animation: fadeIn 0.5s ease-out forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.form-group label {
  margin-bottom: 0.5rem;
  color: #34495e;
  font-weight: 500;
  font-size: 1rem;
}

.form-group input {
  padding: 0.75rem;
  border: 2px solid #bdc3c7;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background-color: #fff;
  color: #2c3e50;
}

.form-group input::placeholder {
  color: #95a5a6;
}

.form-group input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
}

.submit-button {
  background: linear-gradient(135deg, #34495e, #5cb8c2);
  color: #fff;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 1rem;
}

.submit-button:hover {
  opacity: 0.9;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(52, 152, 219, 0.3);
}

.submit-button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
}

.error-message {
  background-color: #ffebee;
  color: #c62828;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1.5rem;
  font-size: 0.9rem;
  border-left: 4px solid #c62828;
}

.itinerary-result {
  margin-top: 2rem;
  background-color: #fff;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
}

.itinerary-result h3 {
  color: #2c3e50;
  margin-bottom: 1.5rem;
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
}

.itinerary-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 2rem;
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 10px;
}

.hotel-info,
.transport-info,
.cost-breakdown {
  background-color: #dfeefc;
  padding: 1.5rem;
  border-radius: 10px;
  margin-bottom: 1.5rem;
}

.hotel-info h4,
.transport-info h4 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.daily-itinerary {
  display: flex;
  flex-wrap: wrap;
  background-color: #dfeefc;

}
.cost-breakdown p
{
  margin-top: 10px;
  font-size: 16px;
}

.day-itinerary {
  padding: 1.5rem;
  border-radius: 0 10px 10px 0;
  transition: all 0.3s ease;
  margin: 0 auto;
}

.day-itinerary:hover {
  transform: translateX(5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.day-itinerary h4 {
  color: #2c3e50;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.day-detail p {
  margin: 0.3rem 0;
}

.confirm-button {
  background: linear-gradient(135deg, #34495e, #5cb8c2);
  color: #fff;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-top: 1.5rem;
  display: block;
  width: 100%;
}

.confirm-button:hover {
  opacity: 0.9;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(39, 174, 96, 0.3);
}

@media (max-width: 768px) {
  .itinerary-form-container {
    margin: 1rem;
    padding: 1.5rem;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .submit-button,
  .confirm-button {
