  border-radius: 4px;
  margin-top: 10px;
  margin-left: 450px;
  width: 250px;

}

.total-cost span {
  font-weight: 600;
  margin-right: 5px;
}

/* Buttons */
.action-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.back-button,
.save-button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.back-button {
  background: linear-gradient(135deg, #2c3e50, #4ca1af);
  color:white;
}

.back-button:hover {
  opacity: 0.9;;
}

.save-button {
  background: linear-gradient(135deg, #2c3e50, #4ca1af);
  color: white;
}

.save-button:hover {
  opacity: 0.9;
