  }
  
  .days-grid {
    grid-template-columns: 1fr;
  }
  
  .cost-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .summary-details {
    flex-direction: column;
  }
  
  .action-buttons {
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
  }
  
  .back-button, .save-button {
    width: 100%;
  }
