    gap: 5px;
}

.profile-content .profile-field label {
    font-weight: bold;
    color: #444;
}

.profile-content .profile-input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 16px;
}

/* Edit Button */
.profile-content .edit-btn {
    margin-top: 15px;
    padding: 10px;
    background: #5cb8c2;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 6px;
    font-size: 16px;
    transition: background 0.3s ease;
}

.profile-content .edit-btn:hover {
    background: #4aa5af;
}

/* Itineraries and Vouchers Sections */
.profile-content .itineraries, .vouchers {
    display: flex;
    flex-direction: column; /* Stack elements instead of wrap */
    grid-gap: 15px;
    gap: 15px;
}

/* Cards for Itineraries and Vouchers */
.profile-content .itinerary-card, .voucher-card {
    padding: 15px;
    border-radius: 8px;
    background: white;
    border: 1px solid #ddd;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
}

/* Redeem Button */
.profile-content .voucher-card button {
    padding: 8px;
    background: #28a745;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 6px;
    font-size: 14px;
    transition: background 0.3s ease;
}

.profile-content .voucher-card button:hover {
    background: #218838;
}

/* Redeemed Voucher Styling */
.profile-content .redeemed-voucher {
    color: #28a745;
    font-weight: bold;
    font-size: 16px;
    text-align: center;
    padding: 10px;
    border-radius: 5px;
    background: #eaf6eb;
}

