.checkout-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f3f4f6 0%, #e5e7eb 100%);
  padding: 2rem;
  width: 100%;
}

.checkout-content {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 2rem;
}

.checkout-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.checkout-header h1 {
  font-size: 2rem;
  font-weight: 700;
  color: #1a1f36;
}

.secure-badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: #ecfdf5;
  color: #059669;
  border-radius: 1rem;
  font-weight: 500;
  font-size: 0.875rem;
}

.checkout-grid {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 2rem;
}

/* Card Styles */
.summary-card,
.payment-card {
  background: white;
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.summary-card:hover,
.payment-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #f3f4f6;
  padding-top: 30px;
}

.card-header h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1a1f36;
  padding-top: 30px;
}

/* Cost Breakdown Styles */
.cost-breakdown {
  background: #f8fafc;
  padding: 1.5rem;
  border-radius: 0.75rem;
}

.cost-item {
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 0;
  color: black;
  font-size: 0.875rem;
}

.cost-divider {
  height: 1px;
  background: #e5e7eb;
  margin: 1rem 0;
}

.cost {
  font-weight: 500;
  color: #1a1f36;
}

.cost-discount {
  color: #059669;
  font-weight: 500;
}

.cost-total {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1a1f36;
}

/* Promo Section Styles */


.promosection h3 {
  font-size: 1rem;
  color: #4b5563;
  margin-bottom: 0.75rem;
}

.promo-input {
  display: flex;
  gap: 0.75rem;
}

.promo-input input {
  flex: 1;
  padding: 0.75rem 1rem;
  border: 2px solid #e5e7eb;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  transition: border-color 0.2s;
}

.promo-input input:focus {
  outline: none;
  border-color: #6366f1;
}

.promo-button {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #2c3e50, #4ca1af);
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: opacity 0.2s;
}

.promo-button:hover {
  opacity: 0.9;
}

/* Payment Form Styles */
.payment-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.stripe-card {
  padding: 1rem;
  border: 2px solid #e5e7eb;
  border-radius: 0.75rem;
  background: #f9fafb;
  transition: all 0.2s ease;
}

.stripe-card.focused {
  border-color: #6366f1;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

.payment-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem;
  background: linear-gradient(135deg, #2c3e50, #4ca1af);
  color: white;
  border: none;
  border-radius: 0.75rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.payment-button:hover:not(:disabled) {
  opacity: 0.9;
  transform: translateY(-1px);
}

.payment-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.payment-button.loading {
  background: #142958;
}

/* Messages */
.error-message,
.success-message {
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  animation: slideIn 0.3s ease;
}

.error-message {
  background: #fef2f2;
  border: 1px solid #fee2e2;
  color: #dc2626;
}

.success-message {
  background: #f0fdf4;
  border: 1px solid #dcfce7;
  color: #059669;
}

.security-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
  padding: 0.75rem;
  background: #f8fafc;
  border-radius: 0.5rem;
  color: #64748b;
  font-size: 0.875rem;
}

/* Animations */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-in {
  animation: slideIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 0.5s ease;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .checkout-grid {
    grid-template-columns: 1fr;
  }
  
  .checkout-content {
    padding: 1rem;
  }
}

@media (max-width: 640px) {
  .checkout-container {
    padding: 1rem;
  }
  
  .checkout-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .cost-item {
    flex-direction: column;
    gap: 0.5rem;
  }
}